import {h} from 'preact';
import './survey.scss';
import { useState } from 'preact/hooks';
import { Formik, Form, Field, ErrorMessage } from 'formik';
import posed, { PoseGroup } from 'react-pose';
import {object, number, string} from 'yup';
import {useEffect} from 'preact/hooks'

const Step = posed.div({
    enter: {opacity:1},
    exit: {opacity:0}
})

const Modal = posed.div({
    show: {opacity:1,  applyAtStart: {display:'flex'}},
    hide: {opacity:0,  applyAtEnd: {display:'none'}}
})

const FieldOptions = ({name, options, setFieldValue, value}) => {
    return (
        <div className={'options'}>
            {options.map((o, index) => (
                <label className="radioInput">
                    <input type="radio" className="radioInputStyled" value={index} checked={value === index} onChange={e => setFieldValue(name, index)} />
                    {o}
                </label>
            ))}
        </div>
    )
}

const Survey = (props) => {

    const [step, setStep] = useState(0);
    const [showModal, setShowModal] = useState(false)
    const [country, setCountry] = useState(props.country)

    useEffect(() => {
        setShowModal(true)
    }, [])

    const questions = {
        ch: [
            {
                t:'Wie alt sind Sie?',
                name:'age',
                placeholder: 'E.g. 30'
            },{
                t: 'Welches Geschlecht haben Sie?',
                options: ['Weiblich','Mnnlich','Divers'],
                name: 'genre'
            },
            {
                t:'Was ist Ihr hchster abgeschlossener oder erhaltener Bildungsabschluss?',
                options: ['Kein formeller Bildungsabschluss','Primarschule oder Sekundarschule (A, B und C)','Berufliche Grundbildung (eidg. Berufsattest oder Fhigkeitszeugnis mit Berufsmaturitt)','Gymnasiale Maturitt oder Fachmaturitt','Eidg. Berufsprfung oder hhere Fachprfung','Bachelor','Master','Diplom','Promotion'],
                name: 'education'
            },{
                t:'Wie hoch ist das monatlich verfgbare Nettoeinkommen Ihres Haushalts?',
                name: 'income',
                options: ['0  999 Fr.','1000 1999 Fr.','2000  2999 Fr.','3000  3999 Fr.','4000  4999 Fr.','5000  5999 Fr.','6000  6999 Fr.','>= 7000 Fr. ','Keine Angabe'],
            }
        ],
        de: [
            {
                t:'Wie alt sind Sie?',
                name:'age',
                placeholder: 'E.g. 30'
            },{
                t: 'Welches Geschlecht haben Sie?',
                options: ['Weiblich','Mnnlich','Divers'],
                name: 'genre'
            },{
                t:'Was ist Ihr hchster abgeschlossener oder erhaltener Bildungsabschluss?',
                options: ['Kein formeller Bildungsabschluss','Haupt-(Volks-)schulabschluss','Mittlere Reife','Fachhochschulreife','Allg. Hochschulreife','Lehre/Berufsausbildung','Bachelor','Master','Diplom','Promotion'],
                name: 'education'
            }, {
                t:'Wie hoch ist das monatlich verfgbare Nettoeinkommen Ihres Haushalts?',
                name:'income',
                options: ['0  499 ','500  999','1000  1999 ','2000  2999 ','3000  3999 ','4000  4999 ','5000  5999 ','>= 6000 ','Keine Angabe']
            }
        ]
    }
        
    

    


    return (
        <Modal className="modal" pose={showModal ? 'show' :'hide'}>
            <div className="modalInner">
                <Formik 
                    onSubmit={(values, { setSubmitting }) => {
                        setSubmitting(false);

                        // send data to main handler
                        props.callback({
                            ...values,
                            country
                        });
                        setShowModal(false)
                    }}
                    // validate data
                    validationSchema={object({
                        age: number()
                            .max(100,'Older than 100?')
                            .min(18, 'Must be 18 years old')
                            .required('Required'),
                        education: number().integer().required(),
                        income: number().integer().required(),
                        genre: number().integer().required()
                      })}
                      //initialValues={{genre:false,age:'',education:false,income:false}}
                      initialValues={{genre:1,age:'30',education:1,income:1}}

                >{({setFieldValue, values, errors, isSubmitting }) => 
                    <Form>
                        <PoseGroup>
                            {step == 0 && <Step key={0} className="intro">
                                <div className="right">
                                    <h1>Welcome to the study</h1>
                                    <p>Liebe Teilnehmerin, lieber Teilnehmer,<br></br>vielen Dank, dass Sie an dieser Studie teilnehmen. Unser Ziel ist es zu untersuchen, wie Menschen Lebensmittel online einkaufen. Zu diesem Zweck werden Sie spter eine Einkaufsliste mit verschiedenen Produkten erhalten, die Sie in einem Online-Supermarkt bestellen sollen. Die Studie besteht aus insgesamt drei Teilen. Im ersten Teil werden Sie gebeten einen kurzen Fragebogen zu Ihrer Person und Ihrer Lebenssituation auszufllen. Darauf folgt die Online-Shopping Aufgabe. Im letzten Teil folgt wieder ein Fragebogen zu Ihrer Person. </p> 
                                    <p>Fr Ihre Teilnahme an der Studie erhalten Sie eine Vergtung in Hhe von xx/CHF. Eine zustzliche Vergtung erhalten Sie basierend auf den Entscheidungen, die Sie im Verlauf der Studie treffen. Hierzu werden zwei zufllige Produkte, die Sie im Verlauf der Studie in Ihren Warenkorb gelegt haben, ausgewhlt. Sie erhalten entweder diese Produkte am Ende der Studie als zustzliche Vergtung oder den monetren Gegenwert der Produkte. </p>
            
                                    <p>Wir werden all Ihre Antworten vertraulich und anonym erfassen. Anhand Ihrer Antworten werden keine Rckschlsse auf Ihre Person mglich sein. </p>
            
                                    <p>Falls Sie ihre Daten nach Beendigung der Studie zurckziehen mchten, kontaktieren Sie bitte:<br></br>ID Labs ETH/HSG,<br></br>Weinbergstrasse 56, 8092 Zrich, team@autoidlabs.ch</p>
            
                                    <p>Nochmals vielen Dank!</p>
                                    <p>Klaus Fuchs (Projektleitung)<br></br>Prof. Dr. Verena Tiefenbeck<br></br>Jie Lian<br></br>Leonard Michels<br></br>Mehdi Bouguerra</p>
                                    <FieldOptions name={'country'} options={['Germany','Switzerland']} setFieldValue={(e,i) => setCountry(['de','ch'][i])} value={['de','ch'].indexOf(country)}/>                                    
                                    <a className={'next'} onClick={e => setStep(s => s+1)}>Next</a>
                                </div>
                            
                            </Step>}
                            {step == 1 && <Step  key={1} className={'step'}>
                                <h1>Intro Survey</h1>
                                {
                                    questions[country].map(q => (
                                        <div className="question">
                                            <h2>Wie alt sind Sie?</h2>
                                            {q.options ? <FieldOptions name={q.name} options={q.options} setFieldValue={setFieldValue} value={values[q.name]}/> : <Field placeholder={q.placeholder} className={'input'} type="text" name={q.name}/>}
                                            <ErrorMessage className="error" name={q.name} component="div" />
                                        </div>
                                    ))
                                }
                                {Object.keys(errors).length > 0 && <div className="error">Please check answers</div>}
                                <input type="submit" className={'next'} value="Start"/>
                            </Step>}
                        </PoseGroup>
                    </Form>
                }</Formik>
            </div>
        </Modal>
    )
}



export default Survey;